import React, {  useEffect, useState } from 'react';
import { getOther } from '../redux/actions/app'
import Skidki from '../component/Skidki'
import Title from '../component/Title'

const Main = React.memo(
    (props) =>{
        let handleScroll = () => {
            if(window.pageYOffset+window.outerHeight>document.documentElement.offsetHeight-506){
                getBy(false)
            }
        }
        let [items, setItems] = useState([]);
        let getBy = async (news) => {
            let data;
            let _items = [];
            if (!news)
                _items = items;
            data = await getOther({name: 'Скидки', data: {skip: _items.length}});
            if(news||data.length>0&&_items.filter(element => element._id == data[0]._id).length===0) {
                await Array.prototype.push.apply(_items, data);
                await setItems(_items);
                if(window.rerun!=undefined)
                    window.rerun()
            }
        }
        useEffect(()=>{
            window.addEventListener('scroll', handleScroll);
            return ()=>{
                window.removeEventListener('scroll', handleScroll);
            }
        })
        useEffect(async ()=>{
            getBy(true)
        },[]);
        return (
            <>
            <Title title="Со скидкой"/>
            {items.length>2?
                items.map((element, idx) => {
                    if(idx!==0&&(idx+1)%3===0) {
                        return <Skidki history={props.history} items={[items[idx-2], items[idx-1], items[idx]]}/>
                    } else if(idx+1===items.length) {
                        if((idx+1)%2===0)
                            return <Skidki history={props.history} items={[items[idx-1], items[idx]]}/>
                        else
                            return <Skidki history={props.history} items={[items[idx]]}/>
                    }
                })
                :items.length>1?
                    <Skidki history={props.history} items={[items[0], items[1]]}/>
                    :items.length>0?
                        <Skidki history={props.history} items={[items[0]]}/>
                        :null}
            </>
        );
    })

export default Main;
